<template>
<v-app>
  <v-container>
    <v-row>
      <h1>Checkup component {{$route.params.id}}</h1>
      
      <v-col v-for="(value, name) in checkup" :key="name" cols="12">
	<v-card class="mx-auto"  >
	  <v-card-title><strong>{{name}}</strong></v-card-title>
	  <v-list-item v-for="(v, n) in value" :key="n">{{n}}: {{v}}</v-list-item>
	</v-card>
      </v-col>
      
    </v-row>
  </v-container>
  </v-app>
</template>

<script>
export default {
    data: () => ({
	checkup_id: '',
	checkup: {},
    }),
    methods: {
	loadCheckupData(code) {
	    let self = this;
	    this.$http.get(`/checkup/${code}`)
		.then(response => {
		    self.checkup = response.data;
		})
		.catch(() => {

		});
	},
    },
    computed: {
    },
    created() {
	this.checkup_id = this.$route.params.id;
	this.loadCheckupData(this.$route.params.id);
    },
    watch: {
	$route (to){
	    this.checkup_id = to.params.id;
	    this.loadCheckupData(to.params.id);
	}
    } 
}  
</script>
